@import "settings/_nullstyle"
@import "settings/_vars"
@import "settings/_mixins"
@import "settings/_iconFont"
//@import "settings/_theme"

@import "./footer"
@import "./header"
@import "./info"
@import "./projects"
@import "./contacts"
@import "./project"

@import url("https://fonts.googleapis.com/css?family=Montserrat:regular,500,600,700,800,900,500italic&display=swap")
@import url("https://fonts.googleapis.com/css?family=Roboto:regular,500&display=swap")


body
    font-size: 16px
    font-family: Roboto
    flex-wrap: 400
    transition: background 0.2s ease
    @include flex-column
    #root
        flex: 1 1 auto
        overflow-x: hidden
        position: relative
        // height: 100%
        @include flex-column
        .dark-background,.light-background
            position: fixed
            z-index: 0
            top: 0
            left: 0
            width: 100%
            height: 100%
            transition:  0.5s ease
        main
            position: relative
            z-index: 1
            flex: 1 1 auto

html.dark
    body
        background: $mainColor
        color: $colorWhite
        #root
            .light-background
                opacity: 0
            .dark-background
                opacity: 0.6

html.light
    body
        background: $colorWhite
        color: $mainColor
        #root
            .light-background
                opacity: 0.6
            .dark-background
                opacity: 0




._container
    max-width: 1230px
    margin: 0 auto

._title
    font-family: Montserrat
    font-weight: 700
    text-transform: uppercase

section 
    animation: show .4s ease 0s 1

@media ( min-width: $tablet)
    ._title
        font-size: 32px
    .dark-background
        background: url("/icons/bg-dark.svg") top left/100% repeat
    .light-background
        background: url("/icons/bg-light.svg") top left/100% repeat

@media (max-width: $tablet) 
    ._title
        font-size: 32px
    .dark-background
        background: url("/icons/bg-dark.svg") top left/15.5% repeat
    .light-background
        background: url("/icons/bg-light.svg") top left/190% repeat

@media ( min-width: $mobile)
    ._container
        padding: 0 20px

@media (max-width: $mobile) 
    ._container
        padding: 0 10px
    ._title
        font-size: 24px
    .dark-background
        background: url("/icons/bg-dark.svg") top left/23% repeat
    .light-background
        background: url("/icons/bg-light.svg") top left/270% repeat




@media ( min-width: $tablet)

@media (max-width: $tablet) 

@media ( min-width: $mobile)

@media (max-width: $mobile) 

@keyframes show
    0%
        opacity: 0
    100%
        opacity: 1