@import "settings/_vars"
@import "settings/_mixins"

.section-contacts
    padding: 20px 0
    margin-top: 60px
    .contacts-container
        @include flex-column
        .contacts-title
            @include adaptiv-property(margin-bottom, 80 ,30 , 1230 )
        .contacts-block
            display: flex
            .block1,.block2
                @include flex-column
                .contact-item
                    @include flex-center
                    border-radius: 20px
                    transition: all .3s ease
                    .item-icon
                        cursor: pointer
                        border-radius: 10px
                        @include flex-column-center-center
                    .item-info
                        @include flex-column
                        row-gap: 5px
                        .item-title
                            font-weight: 600
                            font-size: 20px
                        .item-link
                            @include flex-center
                            padding: 5px 0 7px
                            column-gap: 10px
                            flex-direction: row-reverse
                            position: relative
                            transition: all 0.3s ease
                            &:before
                                font-size: 12px
                                transition: transform .3s  ease
                            &::after
                                content: ''
                                position: absolute
                                bottom: 0
                                left: 0
                                width: 0%
                                height: 2px
                                color: none
                                transition: all 0.3s ease
                            &:hover
                                &:before
                                    transform: translateX(10px)
                                &::after
                                    width: 100%


html.dark
    .contacts-container
        .contacts-title
            color: $colorWhite
        .contacts-block
            .block1,.block2
                .contact-item
                    border: 1px solid $colorGreen
                    background: $colorGreen
                    .item-icon
                        background: $mainColor
                        &::before
                            color: $colorWhite
                    .item-info
                        .item-title
                            color: $colorBlack
                        .item-link
                            color: $colorBlue
                            &:before
                                color: $colorBlue
                            &::after
                                background: $colorWhite
                            &:hover
                                color: $colorWhite
                                &:before
                                    color: $colorWhite
                                &::after
                                    width: 100%
                    &:hover
                        background: $mainColor
                        border: 1px solid $colorWhite
                        .item-info
                            .item-title,.item-link
                                color: $colorWhite
                            .item-link
                                &::before
                                    color: $colorWhite
                        .item-icon
                            background: $colorWhite
                            &::before
                                color: $mainColor


html.light
    .contacts-container
        .contacts-title
            color: $mainColor
        .contacts-block
            .block1,.block2
                .contact-item
                    border: 1px solid $mainColor
                    background: $mainColor
                    .item-icon
                        background: $colorWhite
                        &::before
                            color: $mainColor
                    .item-info
                        .item-title
                            color: $colorWhite
                        .item-link
                            color: $colorWhite
                            opacity: 0.8
                            &:before
                                color: $colorWhite
                            &::after
                                background: $colorWhite
                            &:hover
                                color: $colorWhite
                                &:before
                                    color: $colorWhite
                                &::after
                                    width: 100%
                    &:hover
                        background: $colorBlue
                        border: 1px solid $colorWhite
                        .item-info
                            .item-title,.item-link
                                color: $colorWhite
                            .item-link
                                &::before
                                    color: $colorWhite
                        .item-icon
                            background: $colorWhite
                            &::before
                                color: $mainColor

@media ( min-width: $tablet)
    .section-contacts
        .contacts-container
            @include flex-column
            .contacts-title
                @include adaptiv-property(margin-bottom, 80 ,30 , 1230,992 )
            .contacts-block
                width: 100%
                @include flex-center-center
                @include adaptiv-property(column-gap, 80 ,30 , 1230,992 )
                .block1,.block2
                    row-gap: 30px
                    width: 50%
                    .contact-item
                        width: 100%
                        max-width: 450px
                        padding: 10px 20px
                        column-gap: 40px
                        .item-icon
                            width: 60px
                            height: 60px
                            border-radius: 15px
                            background: $mainColor
                            @include flex-column-center-center
                            &::before
                                font-size: 28px
                                color: $colorWhite
                        .item-info
                            @include flex-column
                            row-gap: 10px
                            .item-title
                                font-size: 22px
                            .item-link
                                font-size: 14px

@media (max-width: $tablet) 
    .section-contacts
        .contacts-container
            @include flex-column
            .contacts-title
                margin-bottom: 30px
            .contacts-block
                width: 100%
                @include flex-center-center
                column-gap: 30px
                .block1,.block2
                    row-gap: 30px
                    width: 50%
                    .contact-item
                        width: 100%
                        max-width: 380px
                        padding: 10px 20px
                        column-gap: 30px
                        .item-icon
                            width: 60px
                            height: 60px
                            border-radius: 15px
                            background: $mainColor
                            @include flex-column-center-center
                            &::before
                                font-size: 26px
                                color: $colorWhite
                        .item-info
                            @include flex-column
                            row-gap: 10px
                            .item-title
                                font-size: 24px
                            .item-link
                                font-size: 18px

@media ( min-width: $mobile)

@media (max-width: $mobile) 
    .section-contacts
        .contacts-container
            @include flex-column
            .contacts-title
                margin-bottom: 30px
            .contacts-block
                width: 100%
                @include flex-column
                row-gap: 20px
                .block1,.block2
                    row-gap: 20px
                    width: 100%
                    .contact-item
                        width: 100%
                        column-gap: 30px
                        .item-icon
                            width: 50px
                            height: 50px
                            border-radius: 10px
                            background: $mainColor
                            @include flex-column-center-center
                            &::before
                                font-size: 20px
                                color: $colorWhite
                        .item-info
                            @include flex-column
                            row-gap: 5px
                            .item-title
                                font-size: 18px
                            .item-link
                                font-size: 14px
