@import "settings/_vars"
@import "settings/_mixins"

.section-info
    margin-top: 60px
    overflow: hidden
    padding: 20px 0
    .info-container
        @include flex-column
        .info-title
            @include adaptiv-property(margin-bottom, 40 , 15 ,1230 )
        .info-body
            display: flex
            .info-descr
                @include flex-column    
                .descr-title
                    font-family: Montserrat
                    @include adaptiv-property(font-size, 46 , 22 , 1230 )
                    font-weight: 700
                    @include adaptiv-property(margin-bottom, 20 , 10 ,1230 )
                .descr-text
                    @include adaptiv-property(margin-bottom, 30 , 10 ,1230 )
                    @include adaptiv-property(max-width, 620 , 300 ,1230 )
                    line-height: 1.2
                .descr-list
                    @include flex-column
                    row-gap: 10px
                    @include adaptiv-property(margin-bottom, 20 , 10 ,1230 )
                    .list-title
                        text-transform: uppercase
                        font-family: Montserrat
                        font-size: 18px
                        font-weight: 600
                        margin-bottom: 5px
                        @include flex-center
                        column-gap: 5px
                        &::before
                            content: ''
                            width: 15px
                            height: 2px
                    .list-item
                        position: relative
                        @include adaptiv-property(width, 500, 300 , 1230 )
                        @include adaptiv-property(height, 50, 30 , 1230 )
                        border-radius: 10px
                        span
                            font-weight: 500
                            position: relative
                            z-index: 1
                        .item-content
                            z-index: 0
                            position: absolute
                            top: 0
                            left: 0
                            height: 100%
                            border-radius: 7px
                            @include flex-column
                            color: #fff
                            font-weight: 500
                        #HTML
                            animation: scaleHtml 2s ease 1 0s
                            width: 75%
                        #CSS
                            animation: scaleCss 2.5s ease 1 0s
                            width: 85%
                        #JS
                            animation: scaleJs 3s ease 1 0s
                            width: 65%
                        #React
                            animation: scaleReact 3s ease 1 0s
                            width: 40%
                .social-list
                    @include flex-column
                    row-gap: 10px
                    .social-title
                        font-family: Montserrat
                        font-size: 18px
                        font-weight: 600
                        @include flex-center
                        column-gap: 5px
                        margin-bottom: 5px
                        &::before
                            content: ''
                            width: 15px
                            height: 2px
                    .social-link
                        @include flex-center
                        font-weight: 500
                        column-gap: 8px
                        max-width: 120px
                        padding: 5px
                        &::before
                            font-size: 20px
                            transition: all 0.3s ease
            .info-image
                @include adaptiv-property(width, 360 , 240 , 1230 )
                border-radius: 20px
                transform: rotate(10deg)
                transition: all .7s ease
                cursor: pointer
                .image-wrapper
                    display: block
                    transform: rotate(-10deg)
                    transition: all .7s ease
                    img
                        border-radius: 20px
                        max-width: 100%
                        height: 100%
                        filter: grayscale(30%)
                &:hover
                    transform: rotate(-10deg)
                    .image-wrapper
                        transform: rotate(10deg)
                        img
                            filter: grayscale(0%)


html.dark
    .section-info
        color: $colorWhite
        .descr-title
            color: $colorWhite
            span
                color: $colorGreen
        .descr-text
            color: $colorWhite
        .descr-list
            .list-title
                color: $colorWhite
                &::before
                    background: $colorWhite
            .list-item
                border: 2px solid $colorGreen
                span
                    color: $colorWhite
                    opacity: 0.9
                .item-content
                    background: $colorGreen
        .social-list
            .social-title
                color: $colorWhite
                &::before
                    background: $colorWhite
            .social-link
                color: $colorWhite
                &::before
                    color: $colorWhite
                &:hover
                    color: $colorGreen
                    &::before
                        color: $colorGreen
        .info-image
            background: $colorGreen
            &:hover
                background: $colorBlue

html.light
    .section-info
        color: $colorBlack
        .descr-title
            color: $colorBlack
            span
                color: $colorBlue
        .descr-list
            .list-title
                color: $mainColor
                &::before
                    background: $mainColor
            .list-item
                border: 2px solid $mainColor
                span
                    color: $colorWhite
                .item-content
                    background: $mainColor
        .social-list
            .social-title
                color: $mainColor
                &::before
                    background: $mainColor
            .social-link
                color: $colorBlack
                &::before
                    color: $colorBlack
                &:hover
                    color: $colorBlue
                    &::before
                        color: $colorBlue
        .info-image
            background: $mainColor
            &:hover
                background: $colorBlue



@media ( min-width: $tablet)

@media (max-width: $tablet) 

@media ( min-width: $mobile)
    .section-info
        .info-body
            @include flex-between-center
            column-gap: 20px
            @include adaptiv-property(padding-right , 70 , 20, 1230,767 )
            .list-item
                padding: 12px 15px

@media (max-width: $mobile) 
    .section-info
        .info-body
            @include flex-column
            row-gap: 30px
            padding-right: 0
            .list-item
                padding:8px 15px
            .info-image
                margin: 0 auto

@keyframes scaleHtml
    0%
        width: 0%
    100%
        width: 75%
@keyframes scaleCss
    0%
        width: 0%
    100%
        width: 85%
@keyframes scaleJs
    0%
        width: 0%
    100%
        width: 65%
@keyframes scaleReact
    0%
        width: 0%
    100%
        width: 40%