@import "settings/_vars"
@import "settings/_mixins"

.project-page
    margin-top: 60px
    .project-page-container
        @include flex-column
        .project-page-link-all
            font-weight: 500
            opacity: 0.7
            padding: 10px 0
            transition: all .2s ease
        .project-page-title
            font-weight: 700
            text-align: center
            font-family: Montserrat
            text-transform: uppercase
        .project-page-images
            overflow: hidden
            .project-page-image-wrapper
                display: flex
                .project-page-image
                    width: 100%
                    img
                        width: 100%
                        height: 100%
        .project-page-text
            line-height: 1.3
        .project-page-technologies
            @include flex-column
            .project-page-technologies-title
                font-family: Montserrat
                font-weight: 600
                text-transform: uppercase
            .project-page-technologies-list
                @include flex-column
                .project-page-technologies-list-item
                    @include flex-center
                    &::before
                        content: ''
                        margin-right: 5px
                        width: 10px
                        height: 2px
        .project-page-link
            font-weight: 500
            text-decoration: underline
            transition: all .2s ease

html.dark
    .project-page
        .project-page-link-all
            color: $colorGreen
            &:hover
                text-decoration: underline
                color: $colorWhite
        .project-page-title
            color: $colorWhite
        .project-page-image
            box-shadow: 0 0 10px 0 #02020260
        .project-page-text
            color: $colorWhite
        .project-page-technologies
            .project-page-technologies-title
                color: $colorWhite
            .project-page-technologies-list
                .project-page-technologies-list-item
                    color: $colorWhite
                    &::before
                        background: $colorWhite
        .project-page-link
            color: $colorGreen
            &:hover
                color: $colorWhite

html.light
    .project-page
        .project-page-link-all
            color: $colorBlue
            &:hover
                text-decoration: underline
                color: $colorBlack
        .project-page-title
            color: $mainColor
        .project-page-image
            box-shadow: 0 0 10px 0 #0202021f
        .project-page-text
            color: $colorBlack
        .project-page-technologies
            .project-page-technologies-title
                color: $mainColor
            .project-page-technologies-list
                .project-page-technologies-list-item
                    color: $colorBlack
                    &::before
                        background: $colorBlack
        .project-page-link
            color: $colorBlue
            &:hover
                color: $colorBlack


@media ( min-width: $tablet)
    .project-page
        padding: 20px 0
        .project-page-container
            .project-page-link-all
                font-size: 14px
            .project-page-title
                font-size: 32px
                margin-bottom: 20px
            .project-page-image
                margin: 0 auto
                max-width: 800px    
                height: 400px
                margin-bottom: 20px
            .project-page-text
                font-size: 18px
                max-width: 1000px
                margin-bottom: 20px
            .project-page-technologies
                row-gap: 10px
                margin-bottom: 40px
                .project-page-technologies-title
                    font-size: 24px
                .project-page-technologies-list
                    padding-left: 10px
                    row-gap: 5px
                    font-size: 15px
                    .project-page-technologies-list-item
                        font-size: 20px
            .project-page-link
                font-size: 14px
                padding: 10px 0

@media (max-width: $tablet) 
    .project-page
        padding: 10px 0
        .project-page-container
            .project-page-link-all
                font-size: 18px
            .project-page-title
                font-size: 32px
                margin-bottom: 10px
            .project-page-image
                margin: 0 auto
                width: 100%  
                height: 420px
                margin-bottom: 10px
            .project-page-text
                font-size: 20px
                margin-bottom: 20px
            .project-page-technologies
                row-gap: 15px
                margin-bottom: 30px
                .project-page-technologies-title
                    font-size: 24px
                .project-page-technologies-list
                    padding-left: 10px
                    row-gap: 10px
                    font-size: 18px
                    .project-page-technologies-list-item
                        @include flex-center
            .project-page-link
                font-size: 16px
                padding: 10px 0

@media ( min-width: $mobile)

@media (max-width: $mobile) 
    .project-page
        padding: 10px 0
        .project-page-container
            .project-page-link-all
                font-size: 13px
            .project-page-title
                font-size: 24px
                margin-bottom: 10px
            .project-page-image
                margin: 0 auto
                width: 100%  
                height: 220px
                margin-bottom: 10px
            .project-page-text
                font-size: 14px
                margin-bottom: 15px
            .project-page-technologies
                row-gap: 10px
                margin-bottom: 30px
                .project-page-technologies-title
                    font-size: 19px
                .project-page-technologies-list
                    padding-left: 10px
                    row-gap: 10px
                    font-size: 16px
                    .project-page-technologies-list-item
                        @include flex-center
            .project-page-link
                font-size: 14px
                padding: 10px 0