@import "./settings/_mixins"
@import "./settings/_vars"

.footer
    padding: 10px 0
    position: relative
    &::before
        content: ''
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 1px
    .footer-container
        position: relative
        z-index: 1
        padding: 0 15px
        @include flex-between-center
        .footer-nav-list
            @include flex-center
            column-gap: 10px
            .footer-nav-list-link
                width: 40px
                height: 40px
                border-radius: 50%
                @include flex-column-center-center
                column-gap: 10px
                transition: all .2s ease
                &::before
                    font-size: 24px
        .footer-copy
            font-size: 14px

html.dark
    .footer
        border-top: 1px solid $colorWhite
        color: $colorWhite
        &::before
            background: $mainColor
        .footer-container
            .footer-nav-list
                .footer-nav-list-link
                    color: $colorWhite
                    &::before
                        color: $colorWhite
                    &:hover
                        &::before
                            color: $colorGreen

html.light
    .footer
        color: $colorBlack
        &::before
            background: $mainColor
        .footer-nav-list-link
            color: $colorBlack
            &::before
                color: $colorBlack
            &:hover
                &::before
                    color: $colorBlue

