@import "./settings/_mixins"
@import "./settings/_vars"

.header
    backdrop-filter: blur(5px)
    padding: 10px 0
    position: fixed
    z-index: 2
    top: 0
    left: 0
    width: 100%
    min-height: 50px
    transition: transform 0.4s ease
    //transform: translateY(-120%)
    &.show
        //transform: translateY(0)
    .header-container
        position: relative
        z-index: 1
        @include flex-between-center
        .header-logo 
            height: 40px
            width: 40px
            flex: 0 0 40px
            border-radius: 50%
            transition: all 0.2s linear
            position: relative
            z-index: 5
            margin-right: 10px
            &:before,&::after,span 
                position: absolute
                border-radius: 2px
                pointer-events: none
            span 
                top: 3px
                left: 15px
                height: 30px
                width: 4px
                transform: rotate(35deg)
            &::after 
                content: ""
                height: 4px
                width: 18px
                bottom: 9px
                left: 8px
            &::before 
                content: ''
                width: 8px
                height: 4px
                top: 16px
                left: 16px
        .header-nav
            .nav-list
                @include flex-between-center
                column-gap: 20px
                .nav-link
                    @include flex-column
                    font-weight: 500
                    padding-bottom: 3px
                    background: transparent
                    text-transform: uppercase
                    transition: all .2s ease
                    position: relative
                    &::after
                        content: ''
                        position: absolute
                        bottom: 0
                        left: 0
                        width: 0%
                        height: 2px
                        transition: all .2s ease
        .theme-switch
            .switch
                position: relative
                display: inline-block
                width: 50px
                height: 30px
                border-radius: 30px
                input 
                    display: none
                .slider 
                    border-radius: 34px
                    position: absolute
                    width: 52px
                    height: 32px
                    cursor: pointer
                    top: -1px
                    left: -1px
                    transition: all .4s ease
                    &:before 
                        position: absolute
                        content: ""
                        height: 26px
                        width: 26px
                        border-radius: 50%
                        left: 3px
                        top: 3px
                        transition: .4s
                input:checked + .slider 
                    &:before 
                        transform: translateX(18px)


html.dark
    .header
        border-bottom: 1px solid $colorWhite
        color: $colorWhite
        .header-logo 
            background: $colorWhite
            &:hover
                background:  $colorGreen
            &:before,&::after,span 
                background: $colorBlue
        .header-nav
            .nav-list
                .nav-link
                    color: $colorWhite
                    &:hover,&.active
                        color: $colorGreen
                        &::after
                            background: $colorGreen
                            width: 100%
        .switch
            background: $colorWhite
            .slider 
                background: $colorWhite
                &:before 
                    background: $mainColor
                &:hover
                    background: $colorGreen
                    



html.light
    .header
        border-bottom: 1px solid $mainColor
        color: $mainColor
        .header-logo 
            background: $mainColor
            &:hover
                background: $colorBlue
            &:before,&::after,span 
                background: $colorWhite
        .header-nav
            .nav-list
                .nav-link
                    color: $mainColor
                    &:hover,&.active
                        color: $colorBlue
                        &::after
                            background: $colorBlue
                            width: 100%
        .switch
            background: $mainColor
            .slider 
                background: $mainColor
                &:before 
                    background: $colorWhite
                &:hover
                    background: $colorBlue




@media ( min-width: $tablet)
    .header
        .nav-link
            font-size: 20px
@media (max-width: $tablet) 
    .header
        .nav-link
            font-size: 18px

@media ( min-width: $mobile)

@media (max-width: $mobile) 
    .header
        .nav-link
            font-size: 13px


