@import "settings/_vars"
@import "settings/_mixins"

.section-projects
    height: 100%
    margin-top: 60px
    padding: 20px 0
    .projects-container
        width: 100%
        overflow: hidden
        @include flex-column
        position: relative
        .projects-block
            display: grid
            .project
                @include flex-column
                .project-image
                    width: 100%
                    cursor: pointer
                    img
                        width: 100%
                        height: 100%
                        object-fit: cover
                .project-title
                    cursor: pointer
                    font-family: Montserrat
                    text-transform: uppercase
                    font-weight: 600
                    line-height: 1.3
                .project-descr
                    line-height: 1.2
                .project-category
                    text-transform: uppercase

html.dark
    .section-projects
        .projects-container
            .projects-block
                .projects-title
                .project
                    box-shadow: 0 0 10px 0 #222427
                    transition: all .3s ease
                    &:hover
                        transform: scale(1.03)
                        box-shadow: 0 0 20px 0 #1f2021
                    .project-title
                        color: $colorWhite
                        &:hover
                            color: $colorGreen
                    .project-descr
                        color: $colorWhite
                    .project-category
                        color: $colorGreen


html.light
    .section-projects
        .projects-container
            .projects-block
                .projects-title
                    color: $mainColor
                .project
                    box-shadow: 0 0 10px 0 #9c9d9f
                    transition: all .3s ease
                    &:hover
                        transform: scale(1.03)
                        box-shadow: 0 0 20px 0 #565758
                    .project-title
                        color: $mainColor
                        &:hover
                            color: $colorGreen
                    .project-descr
                        color: $colorBlack
                    .project-category
                        color: $colorBlue


@media ( min-width: $tablet)
    .section-projects
        .projects-container
            .projects-title
                margin-bottom: 30px
            .projects-block
                grid-template-columns: repeat(4, minmax(200px, 1fr))
                column-gap: 20px
                row-gap: 30px
                .project
                    padding-bottom: 40px
                    .project-image
                        height: 200px
                        margin-bottom: 20px
                    .project-title
                        font-size: 24px
                        margin-bottom: 15px
                        margin-left: 20px
                    .project-descr
                        font-size: 18px
                        margin-left: 20px
                        margin-bottom: 20px
                    .project-category
                        margin-left: 20px

@media (max-width: $tablet) 
    .section-projects
        .projects-container
            .projects-title
                margin-bottom: 20px
            .projects-block
                grid-template-columns: repeat(2, minmax(200px, 1fr))
                column-gap: 30px
                row-gap: 50px
                .project
                    padding-bottom: 40px
                    .project-image
                        height: 200px
                        margin-bottom: 20px
                    .project-title
                        font-size: 24px
                        margin-bottom: 15px
                        margin-left: 20px
                    .project-descr
                        font-size: 18px
                        margin-left: 20px
                        margin-bottom: 20px
                    .project-category
                        margin-left: 20px


@media ( min-width: $mobile)

@media (max-width: $mobile) 
    .section-projects
        .projects-container
            .projects-title
                margin-bottom: 20px
            .projects-block
                grid-template-columns: repeat(1, minmax(280px, 1fr))
                row-gap: 20px
                .project
                    max-width: 300px
                    margin: 0 auto
                    padding-bottom: 20px
                    .project-image
                        height: 180px
                        margin-bottom: 10px
                    .project-title
                        font-size: 20px
                        margin-bottom: 10px
                        margin-left: 20px
                    .project-descr
                        font-size: 16px
                        margin-left: 20px
                        margin-bottom: 15px
                    .project-category
                        margin-left: 20px
                        font-size: 14px
