@font-face 
    font-family: 'icomoon'
    src:  url('../../fonts/icomoon.woff?1hcstj') format('woff')
    font-weight: normal
    font-style: normal
    font-display: block

[class^="icon-"]::before, [class*=" icon-"]::before
    font-family: 'icomoon' !important
    speak: never
    font-style: normal
    font-weight: normal
    font-variant: normal
    text-transform: none
    line-height: 1
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale

.icon-arrow:before 
    content: "\e903"
.icon-viber:before 
    content: "\e904"
.icon-phone:before 
    content: "\e905"
.icon-email:before 
    content: "\e906"
.icon-instagram:before 
    content: "\e900"
.icon-telegram:before 
    content: "\e901"
.icon-github:before 
    content: "\e902"